export default{
	name: "content",
	data(){
		return{
			num: 0,
			tab:[
				{
					"id":1,
					"name":"推荐",
					"nav":"recomment",
					"title":"猜你喜欢"
				},
				{
					"id":2,
					"name":"美食",
					"nav":"food",
					"title":"馋嘴零食"
				},
				{
					"id":3,
					"name":"书籍",
					"nav":"book",
					"title":"学习资料"
				},
				{
					"id":4,
					"name":"美妆",
					"nav":"cosmetics",
					"title":"超值精选"
				},
				{
					"id":5,
					"name":"手机",
					"nav":"phone",
					"title":"低价备用"
				},
				{
					"id":6,
					"name":"杂货",
					"nav":"goods",
					"title":"惊喜淘宝"
				}
			]
		}
	},
	methods:{
		clickTabs(index,nav){
			this.num = index;
			//点击切换loading状态显示
			let loading = true
			//当tab切换的时候pageid置零
			let pageid = 0
			//tab切换把上拉加载组件隐藏
			let uniload =false
			//tab切换把没有数据提示的组件隐藏
			let nonedata = false
			
			//对象的形式传值到vuex中
			let pullobj = {
				loading:loading,
				nav:nav,
				pageid:pageid,
				uniload:uniload,
				nonedata:nonedata
			}
			this.$store.commit('navmuta',pullobj)
			
			
			//请求数据库
			//上拉加载值
			let listid = 0
			homeList(nav,listid)
			.then((res)=>{
				//tab切换没有数据的情况
				if(res.data.length == 0){
					let nonedata = true
					this.$store.commit('nonemuta',nonedata)
				}else{
					let nonedata = false
					this.$store.commit('nonemuta',nonedata)
				}
				//vuex传值
				let listdata = res.data
				this.$store.commit('listmuta',listdata)
				//点击切换loading状态显示
				let loading = false
				this.$store.commit('loadmuat',loading)
			})
			.catch((err)=>{
				console.log(err)
			})
		}
	},
	
}
